<template>
    <div class="sjs-logo-qrcode__container"
         :style="containerStyle">
        <img ref="image"
             v-show="done && mode === 'image'"
             alt="qrcode">
        <canvas ref="canvas"
                v-show="done && mode === 'canvas'"
                alt="qrcode"></canvas>
    </div>
</template>

<script>
import QrCodeWithLogo from 'qr-code-with-logo'
import LocalImage from '../assets/logo.png'
const METHOD_MAP = {
    image: 'toImage',
    canvas: 'toCanvas'
}

export default {
    name: 'VueLogoQrcode',
    data() {
        return {
            done: false // 
        }
    },
    props: {
        // 
        content: {
            type: String,
            default: ''
        },
        // 
        width: {
            type: Number,
            defaule: 100
        },
        // logo url
        logoSrc: {
            type: String,
            default: LocalImage
        },
        // logo 
        logoRadius: {
            type: Number,
            default: 8
        },
        //  
        autoGen: {
            type: Boolean,
            default: false
        },
        // 
        showImmediately: {
            type: Boolean,
            default: true
        },
        mode: {
            type: String,
            default: 'image',
            validator: function (value) {
                //  image canvas
                return ['image', 'canvas'].indexOf(value) !== -1
            }
        }
    },
    computed: {
        containerStyle() {
            return {
                width: this.width + 'px',
                height: this.width + 'px'
            }
        }
    },
    methods: {
        // 
        genQrCode(options) {
            options = options || this._$getGenOptions();
            QrCodeWithLogo[METHOD_MAP[this.mode]](options).then(() => {
                this.done = true;
            })
        },
        // 
        genQrCodeImageDownload() {
            let options = this._$getGenOptions(true);
            QrCodeWithLogo.toImage(options);
        },
        // ,
        _$addWatchs() {
            if (this._$watchs) {
                return;
            } else {
                this._$watchs = Object.keys(this.$options.props).filter(item => !['autoGen', 'showImmediately'].includes(item)).map(item => {
                    this.$watch(item, this.genQrCode);
                })
            }
        },
        // ,
        _$removeWatchs() {
            this._$watchs && this._$watchs.forEach(item => item());
            this._$watchs = null;
        },
        // ,
        _$getGenOptions(download) {
            let options = {
                image: this.$refs.image,
                canvas: this.$refs.canvas,
                content: this.content,
                width: this.width,
                logo: {
                    src: this.logoSrc,
                    radius: this.logoRadius
                }
            }
            // 
            download && (options.download = true);
            return options;
        }
    },
    created() {
        // 
        if (this.autoGen) {
            this._$addWatchs()
        }
    },
    mounted() {
        // 
        if (this.content && this.showImmediately) {
            this.genQrCode();
        }
    },
    watch: {
        autoGen(newVal) {
            if (newVal) {
                this._$addWatchs();
            } else {
                _$this.removeWatchs();
            }
        }
    }
}
</script>

<style lang="scss" scoped>
.sjs-logo-qrcode__container {
    display: inline-block;
    vertical-align: baseline;
    canvas {
        height: 100%;
        width: 100%;
    }
    img {
        height: 100%;
        width: 100%;
    }
}
</style>



